
.o-createaccount-container{
    margin: 20px 0px;
    width: 90vw;
    height: 78vh;
    background-color: #7CC836;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.o-createaccount-info-container{
    margin: 5% 0% 2% 0%;
    min-width: 36%;
    min-height: 60%;
    color: white;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    align-self: center;
}

.o-select{
    border-radius: 10px;
    border: 2px solid white;
    background-color: transparent;
    color: white;
    font-size: 16px;
    padding: 5px 5px;
}

.o-createaccount-button{
    height: 50px;
    margin-left: 75vw;
    justify-self: flex-end;
}